const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			collectionList: {},
			imgUrl: '',
			isShow: true,
			cancelShow: true,
			inputValue: ''
		};
	},
	components: {
		
	},
	onShow() {
		this.content();
	},
	onLoad() {
		this.imgUrl = helper.imgUrl;
	},
	methods: {
		withdraw(){
			uni.navigateTo({
				url: `/pages/cashOut`
			})
		},

		goMore(){
			this.isShow = false
		},
		quickInlet(e) {
			uni.navigateTo({
				url: `/pages/shareCode?type=${e}`
			})

		},
		cancel(type) {
			this.$refs['show' + type].close();
		},
		content() {
			uni.request({
				url: `${helper.requestUrl}/promote-rebates`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					// uni.hideLoading();
					res = helper.null2str(res);
					console.log(res,'++++');
					if (res.data.status_code == 200) {
						this.collectionList = res.data;
					} else {

					}
				}
			});
		},
		// 团队列表
		goTeam(e){
			uni.navigateTo({
				url: `/pages/teamList`
			})
		},
		// 返佣设置
		cashSet(){
			uni.navigateTo({
				url: `/pages/commissionSet`
			})
		}
	}
};
